import React from "react";
import { Link, graphql, useStaticQuery } from "gatsby";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import styled from "styled-components";

const Image = styled.div`
  .gatsby-image-wrapper {
    max-width: 400px;
    max-height: 400px;
    border: solid 5px #eb219b;
    border-radius: 20%;
    margin: auto;
    overflow: hidden;
  }
  img {
    border-radius: 20%;
  }
`;

const IntroAbout = () => {
  const { data, imgProfil } = useStaticQuery(
    graphql`
      query {
        data: dataJson {
          basics {
            summary
          }
        }
        imgProfil: file(
          relativePath: { eq: "images/IMG_20160523_131308.jpg" }
        ) {
          childImageSharp {
            gatsbyImageData(quality: 50, width: 500, placeholder: BLURRED)
          }
        }
      }
    `
  );
  const image = getImage(imgProfil);
  return (
    <section className="section">
      <div className="container">
        <div className="columns is-vcentered">
          <div className="column is-centered">
            <Image>
              <GatsbyImage image={image} alt="Me in the 80's" />
            </Image>
          </div>
          <div className="column">
            <h2 className="title">Hello</h2>
            <p className="jiu-mb2">{data.basics.summary}</p>
            <Link to="/experiences" className="button">
              Experiences {">>"}
            </Link>
          </div>
        </div>
      </div>
    </section>
  );
};

export default IntroAbout;
